async function backend_tester_fn() {
  var eid = localStorage.getItem("connecteid");
  var motdepasse = localStorage.getItem("connectmdp");
  const rep = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_connecter_bd?eid=${eid}&motdepasse=${motdepasse}`) ;   
  if (rep.ok){
    rep.json().then((data)=>{ 
      if (data.length != 0) {
        if (data[0].admin == 1) {
          (document.getElementById("connectnomm") as HTMLFormElement).insertAdjacentHTML("afterbegin",data[0].nom+" ") ;
        }
        if (data[0].admin == 0) {
          window.location.href = '/accueilemploye' ;
        }
      }
      else {
        localStorage.removeItem("connecteid");
        localStorage.removeItem("connectmdp");
        localStorage.removeItem("connectnom");
        window.location.href = '/connecter' ;
      }
    }) 
  } else {
    localStorage.removeItem("connecteid");
    localStorage.removeItem("connectmdp");
    localStorage.removeItem("connectnom");
    window.location.href = '/connecter' ;
  }
} ;




async function backend_ajouter_chercher_tache_fn() {

  var d1 = document.getElementById('ac') ;
  var t = (d1 as HTMLFormElement).innerHTML ;
  var c = 0 ;
  do {
    c++ ;
    t = t.replace('id="t',"|") ;
  } while (t.includes('id="t') == true) ;

  

  (document.getElementById("dt1") as HTMLFormElement).innerHTML = 	`<h2> <select style="width: 286px; font-size:18px;" name="dt1" id="dt1"> <option value="0">Directeur de cette Tâche (EID)</option> </select> </h2>`
  
  const rep = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_ajouter_chercher_tache_bd`) ;   

  if (rep.ok){
    rep.json().then((data)=>{ 

      for(var j=0; j<data.length;j++) {
        
        (document.getElementById("dt1") as HTMLFormElement).insertAdjacentHTML("beforeend",`<option value="${data[j].eid}">${data[j].eid}</option>`) ; 
      }

    })
}


  
  for (var x=1 ; x < c ; x++) {
    

    (document.getElementById("dt"+x) as HTMLFormElement).innerHTML = 	`<h2> <select style="width: 286px; font-size:18px;" name="dt`+x+`" id="dt`+x+`"> <option value="0">Directeur de cette Tâche (EID)</option> </select> </h2>`
  
    const rep = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_ajouter_chercher_tache_bd`) ;   
  
    if (rep.ok){
      rep.json().then((data)=>{ 

        for(var j=0; j<data.length;j++) {
          
          (document.getElementById("dt"+x) as HTMLFormElement).insertAdjacentHTML("beforeend",`<option value="${data[j].eid}">${data[j].eid}</option>`) ; 
        }

      })
  }

}

  }

async function backend_modifier_chercher_fn() {

  var eid = (document.getElementById("eid") as HTMLFormElement).value ;
  const rep = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_modifier_chercher_bd?eid=${eid}`) ;   
  

  if (rep.ok){

    rep.json().then((data)=>{ 

      (document.getElementById('eid') as HTMLFormElement).name =  (document.getElementById('eid') as HTMLFormElement).value ;

      (document.getElementById('cin') as HTMLFormElement).value =  data[0].cin ;
      (document.getElementById('cin') as HTMLFormElement).disabled =  false ;
      (document.getElementById('cin') as HTMLFormElement).name =  data[0].cin ;


      (document.getElementById('motdepasse') as HTMLFormElement).value =  data[0].mdp ;
      (document.getElementById('motdepasse') as HTMLFormElement).disabled =  false ;

      if (data[0].admin == 1) {  (document.getElementById('admincb') as HTMLFormElement).checked = true ;}  
      (document.getElementById('admincb') as HTMLFormElement).disabled =  false ;

      (document.getElementById('prenom') as HTMLFormElement).value =  data[0].prenom ;
      (document.getElementById('prenom') as HTMLFormElement).disabled =  false ;

      (document.getElementById('nom') as HTMLFormElement).value =  data[0].nom ;
      (document.getElementById('nom') as HTMLFormElement).disabled =  false ;

      (document.getElementById('dn') as HTMLFormElement).value =  data[0].datenaissance ;
      (document.getElementById('dn') as HTMLFormElement).disabled =  false ;

      (document.getElementById('ville') as HTMLFormElement).value =  data[0].ville ;
      (document.getElementById('ville') as HTMLFormElement).disabled =  false ;

      (document.getElementById('email') as HTMLFormElement).value =  data[0].email ;
      (document.getElementById('email') as HTMLFormElement).disabled =  false ;

      (document.getElementById('tel') as HTMLFormElement).value =  data[0].numtel ;
      (document.getElementById('tel') as HTMLFormElement).disabled =  false ;

      (document.getElementById('salaire') as HTMLFormElement).value =  data[0].salaire ;
      (document.getElementById('salaire') as HTMLFormElement).disabled =  false ;

      (document.getElementById('image') as HTMLFormElement).disabled =  false ;

      (document.getElementById('modifiersub') as HTMLFormElement).disabled =  false ;

    })


    const rep2 = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_modifier_chercher_tache_bd?eid=${eid}`) ;   
  
    if (rep2.ok){
      rep2.json().then((data)=>{ 
  

          if (data.length == 0) {

            var audio = new Audio('../assets/aud/miss.ogg');
            audio.play();

            alert("Aucun cin n'a été trouvé dans la base de données ")



          } else {
            var audio = new Audio('../assets/aud/loaded.ogg');
            audio.play();
        }

        (document.getElementById("ac") as HTMLFormElement).innerHTML = `    <div id="ac">   </div>`
        
        for(var i=0 ; i<data.length;i++) { 

          var j = i + 1 ;
 
          (document.getElementById('ajoutertac') as HTMLFormElement).disabled = false ;
          (document.getElementById('ac') as HTMLFormElement).insertAdjacentHTML("beforeend",`<h2>
          <input type="text" size=30 style="font-size:18px;" name="t`+ j +`" id="t`+ j +`" value=" ${data[i].tachenom}">
          </h2>
          <h2>
          <select style="width: 286px; font-size:18px;" name="dt`+ j +`" id="dt`+ j +`"> <option value="0">Directeur de cette Tâche (EID)</option> </select>
          </h2> <br>
          ` ) ;
          backend_ajouter_chercher_tache_fn()
        }
      }) 
    }
  

    


}



} ;







function ajoutertachefn() {
  var d1 = document.getElementById('ac') ;
  var t = (d1 as HTMLFormElement).innerHTML ;
  var c = 1 ;
  do {
    c++ ;
    t = t.replace('id="t',"|") ;
  } while (t.includes('id="t') == true) ;

  var code = '<h2><input type="text" size=30 style="font-size:18px;" name="t' + c + '" id="t' + c + '" value="Nom de Tâche" pattern="[A-z0-9À-ž\s]{3,30}"></h2> <h2> <select style="width: 286px; font-size:18px;" name="dt' + c + '" id="dt' + c + '"> <option value="0">Directeur de cette Tâche (EID)</option> </select></h2> <br>' ;
  (d1 as HTMLFormElement).insertAdjacentHTML('beforeend',code) ;

  backend_ajouter_chercher_tache_fn() ;
 
}

